import {
	Contract,
	BigNumber,
	ethers,
	BrowserProvider
} from 'ethers'
import poolAbi from '@/contracts/pool.json';
import storageAbi from '@/contracts/storage.json';
import Token from '@/handlers/token';
import {
	formatNumber,
	getRandomInt,
	toUint256Hex
} from "@/utils/index.js"
import $envConfig from '@/common/config.js';
class Pool {
	constructor(address = '', provider = null, signer = null, tokenAddress) {
		this.internalAddress = address; // TODO .toLowerCase();
		this.provider = provider;
		this.signer = signer;
		this.tokenAddress = tokenAddress;
		if (!this.address.match(/0x[a-fA-F0-9]{40}/)) return new Error('Missing address');
		this.instance = new Contract(address, poolAbi, signer);
		console.log(tokenAddress, signer)
		this.tokenInstance = new Token(tokenAddress, signer);
		console.log($envConfig.VUE_STORAGE_CONTRACT)
		this.storageInstance = new Contract($envConfig.VUE_STORAGE_CONTRACT, storageAbi, signer);
	}

	get address() {
		return this.internalAddress;
	}

	getPoints(from = '') {
		return new Promise((resolve, reject) => {
			this.instance.getPoints(from)
				.then((points) => Number(points))
				.then(resolve)
				.catch(reject);
		});
	}

	getUplines(from = '') {
		return new Promise((resolve, reject) => {
			this.instance.getUplines(from)
				.then(resolve)
				.catch(reject);
		});
	}

	getUsers(from = '') {
		return new Promise((resolve, reject) => {
			this.storageInstance.getUser(from)
				.then(resolve)
				.catch(reject);
		});
	}

	isRegistered(from = '') {
		return new Promise((resolve, reject) => {
			this.storageInstance.isRegistered(from)
				.then(resolve)
				.catch(reject);
		});
	}

	getGlobalData() {
		return new Promise((resolve, reject) => {
			this.storageInstance.getGlobalStats()
				.then(resolve)
				.catch(reject);
		});
	}

	getRegisterCount(){
		return new Promise((resolve, reject) => {
			this.storageInstance.registeredCount()
				.then((points) => Number(points))
				.then(resolve)
				.catch(reject);
		});
	}

	calcRewards() {
		return new Promise((resolve, reject) => {
			this.instance.calcRewards()
				.then(resolve)
				.catch(reject);
		});
	}

	register(refer = '', from = '') {
		return new Promise((resolve, reject) => {
			if (!this.baseFee) {
				this.baseFee = $envConfig.baseFee;
			}
			if(!refer){
				refer = ethers.ZeroAddress;
			}
			let random = getRandomInt(1, 1000);
			let realValue = Number(this.baseFee + random / Math.pow(10, 8)).toFixed("8");
			this.instance.register.estimateGas(refer).then((gas) => {
				console.log(gas);
				return this.instance.register(refer, {
					gasLimit: gas})
			}).then(resolve)
				.catch(reject);
		});
	}

	checkIn(from = '') {
		// eslint-disable-next-line no-async-promise-executor
		return new Promise(async (resolve, reject) => {
			if (!this.baseFee) {
				this.baseFee = $envConfig.baseFee;
			}
			let random = 0;//getRandomInt(1, 1000);
			let realValue = Number(this.baseFee + random / Math.pow(10, 8)).toFixed("8");
			console.log("need gas", realValue)
			this.instance.checkIn.estimateGas({
				value: ethers.parseEther(realValue)
			}).then((gas) => {
				console.log("gas", gas);
				return this.instance.checkIn({
					gasLimit: gas,
					value: ethers.parseEther(realValue)
				})
			}).then(resolve).catch(reject);
		});
	}

	claimReward(from = '') {
		// eslint-disable-next-line no-async-promise-executor
		return new Promise(async (resolve, reject) => {
			if (!this.baseFee) {
				this.baseFee = $envConfig.baseFee;
			}
			let random = 0;//getRandomInt(1, 1000);
			let realValue = Number(this.baseFee + random / Math.pow(10, 8)).toFixed("8");
			console.log("need gas", realValue)
			this.instance.claimRewards.estimateGas().then((gas) => {
				console.log("gas", gas);
				return this.instance.claimRewards({
					gasLimit: gas
				})
			}).then(resolve).catch(reject);
		});
	}

}

export default Pool;