import Vue from 'vue'
import Vuex from 'vuex'
import {
	ethers,
	BrowserProvider,
	Contract,
	BigNumber
} from "ethers";
import Token from '@/handlers/token';
import Pool from '@/handlers/pool.js';
import $envConfig from '@/common/config.js';
import {
	erc20Abi,
	vantTokenAbi
} from '@/contracts';

import {
	formatNumber,
	getRandomInt
} from "@/utils/index.js"

Vue.use(Vuex)


let lifeData = {}

// 尝试获取本地是否存在lifeData变量，第一次启动时不存在
try {
	lifeData = window.localStorage.getItem('lifeData')
} catch (e) {

}

// 标记需要永久存储的变量，在每次启动时取出，在state中的变量名
let saveStateKeys = ['vuex_user']

// 保存变量到本地存储
const saveLifeData = function (key, value) {
	// 判断变量是否在存储数组中
	if (saveStateKeys.indexOf(key) != -1) {
		// 获取本地存储的lifeData对象，将变量添加到对象中
		let tmpLifeData = window.localStorage.getItem('lifeData')
		// 第一次启动时不存在，则放一个空对象
		tmpLifeData = tmpLifeData ? tmpLifeData : {},
			tmpLifeData[key] = value
		// 将变量再次放回本地存储中
		window.localStorage.setItem('lifeData', tmpLifeData)
	}
}


const store = new Vuex.Store({
	state: {
		walletAddress: null, // 当前连接的钱包地址
		chainId: null, // 当前区块链网络ID
		isConnected: false, // 连接状态标识
		provider: null, // ethers.js的Provider实例
		signer: null,
		appTokenInstance: null,
		poolInstance: null,
		parentAddress: -1, //邀请人地址
		lastMintTime: null,
		user: null,
		isChainCorrect: false,
		points: 0,
		isRegistered: false
	},
	mutations: {
		SET_WALLET(state, {
			address,
			chainId
		}) {
			state.walletAddress = address
			state.chainId = chainId
			state.isConnected = !!address
			state.isChainCorrect = $envConfig.DEFAULT_CHAINID != chainId;
		},
		SET_PROVIDER(state, provider) {
			state.provider = provider
		},
		SET_SIGNER(state, signer) {
			state.signer = signer
		},
		SET_APP_TOKEN_INSTANCE(state, instance) {
			state.appTokenInstance = instance;
		},
		SET_POOL_INSTANCE(state, instance) {
			state.poolInstance = instance;
		},
		SET_PARENT_ADDRESS(state, address) {
			state.parentAddress = address
		},
		SET_LASTMINTTIME(state, timestamp) {
			state.lastMintTime = timestamp;
		},
		SET_USERINFO(state, user) {
			state.user = user;
		},
		SET_IS_REGISTER(state, isRegistered) {
			state.isRegistered = isRegistered;
		}
	},
	actions: {
		$tStore(state, payload) {
			// 判断是否多层调用，state中为对象存在的情况，例如user.info.score = 1
			let nameArr = payload.name.split('.')
			let saveKey = ''
			let len = nameArr.length
			if (len >= 2) {
				let obj = state[nameArr[0]]
				for (let i = 1; i < len - 1; i++) {
					obj = obj[nameArr[i]]
				}
				obj[nameArr[len - 1]] = payload.value
				saveKey = nameArr[0]
			} else {
				// 单层级变量
				state[payload.name] = payload.value
				saveKey = payload.name
			}

			// 保存变量到本地中
			saveLifeData(saveKey, state[saveKey])
		},
		async connectWallet({
			commit
		}) {
			if (window.ethereum) {
				try {
					const provider = new BrowserProvider(window.ethereum);

					const accounts = await provider.send('eth_requestAccounts', []);
					const signer = await provider.getSigner();
					const address = await signer.getAddress();
					const network = await provider.getNetwork();


					commit('SET_PROVIDER', provider);
					commit('SET_SIGNER', signer);

					commit('SET_WALLET', {
						address,
						chainId: network.chainId
					})

					localStorage.setItem('walletState', JSON.stringify({
						address: address.toString(),
						chainId: network.chainId.toString()
					}))


					if (!store.state.isChainCorrect) {
						// await store.dispatch("getUserInfo", {
						// 	account: address.toString()
						// })
						// await store.dispatch("getTokenInstance")
						// await store.dispatch("getVaultMintInstance")
						// await store.dispatch("getVaultClaimInstance")
						await store.dispatch("getPoolInstance")
					} else {
						console.log(123)
					}
				} catch (error) {
					console.error('連接失敗:', error)
				}
			} else {
				console.log("請安裝MetaMask擴展插件")
			}
		},
		// 断开连接
		disconnectWallet({
			commit
		}) {
			commit('SET_WALLET', {
				address: null,
				chainId: null
			})
			localStorage.removeItem('walletState')
		},
		async getUserInfo({
			commit
		}) {
			let instance = store.state.poolInstance;
			let user = await instance.getUsers(store.state.walletAddress);
			// console.log(user);
			if (user) {
				commit("SET_USERINFO", {
					uplines: user[0],
					downlines: user[1],
					lastCheckIn: user[2].toString(),
					checkInCountToday: user[3].toString(),
					tokenRewardCount: user[4].toString(),
					pointCount: user[5].toString(),
					totalPointsClaimed: user[6].toString(),
					checkInTotalCount: user[7].toString(),
					claimedExtraTiers: user[8].toString(),
					dailyCheckInCount: user[9].toString(),
					lastCheckInDay: user[10].toString()
				})
			}
			let isRegistered = await instance.isRegistered(store.state.walletAddress);

			commit("SET_IS_REGISTER", isRegistered ? true : false)
		},

		async getTokenInstance({
			commit
		}) {
			let instance = new Token($envConfig.VUE_APP_TOKEN, store.state.signer);
			commit("SET_APP_TOKEN_INSTANCE", instance);
		},
		async getPoolInstance({
			commit
		}) {
			let instance = new Pool($envConfig.VUE_MAIN_CONTRACT, store.state.provider, store.state
				.signer, $envConfig.VUE_APP_TOKEN);
			commit("SET_POOL_INSTANCE", instance);
			store.dispatch("getUserInfo");
		},
		async bindParentAddress({
			commit
		}, {
			from,
			address
		}) {

		},
		async changeChain({
			commit
		}) {
			if (window.ethereum) {
				try {
					await window.ethereum.request({
						method: "wallet_switchEthereumChain",
						params: [{
							chainId: $envConfig.DEFAULT_CHAINID
						}],
					});
					return true;
				} catch (e) {
					const err = e;
					console.log(err);
					if (err.code === 4902) {
						try {
							await window.ethereum.request({
								method: "wallet_addEthereumChain",
								params: [$envConfig.chainConfig[$envConfig.DEFAULT_CHAINID]],
							});
						} catch (addError) {
							console.error(addError);
						}
					} else {

					}
					return true;
				}
			} else {
				console.log("請等待初始化完成")
			}
		}
	}
})



window.addEventListener("load", async () => {
	// Modern dapp browsers...
	if (window.ethereum) {
		console.log(window.ethereum)
		// window.web3 = ethereum; //new Web3(ethereum);
		try {
			// Request account access if needed
			await ethereum.enable();
			// Acccounts now exposed
			store.dispatch("connectWallet");

		} catch (error) {
			console.log(error)
			// User denied account access...
		}

		window.ethereum.on('accountsChanged', (accounts) => {
			// Handle the new accounts, or lack thereof.
			// "accounts" will always be an array, but it can be empty.
			// store.dispatch("connectWallet");
			window.location.reload();
		});

		window.ethereum.on('chainChanged', (chainId) => {
			// Handle the new chain.
			// Correctly handling chain changes can be complicated.
			// We recommend reloading the page unless you have good reason not to.
			window.location.reload();
		});

	}
	// Legacy dapp browsers...
	else if (window.web3) {
		// Acccounts always exposed
	}
	// Non-dapp browsers...
	else {
		console.log(
			"Non-Ethereum browser detected. You should consider trying MetaMask!"
		);
		return;
	}
});

export default store